<template>
	<!-- product list -->
	<div class="container">
		<div class="row justify-content-center">
			<div class="col-12">
				<!-- products -->
				<div class="row mt-4">
		      <div class="col-md-3 mb-3" v-for="item in products" :key="item.id">
		        <div class="card border-0 shadow">
		          <div class="u-item-img" :style="{backgroundImage: `url(${item.image})`}">
		          	<router-link class="u-item-cover" :to="`/product/${item.id}`">
		          			<div class="u-item-btn">See More</div>
		          	</router-link>
		          </div>
		          <div class="card-body">
		            <h6 class="card-title mt-2">
		            	<router-link class="text-dark font-weight-bold" :to="`/product/${item.id}`">
			            	{{ item.title }}
			            </router-link>
		            </h6>
		            <p class="card-text">{{ item.content }}</p>
					<div class="h6 text-right old" v-if="item.origin_price">原價<span>{{ item.origin_price}}</span></div>
		            <div class="h6 text-right" v-if="item.price">特價<span>{{ item.price}}</span></div>
		          </div>
		        </div>
		      </div>
		    </div>
			</div>
		</div>
	</div>
</template>

<script>
import $ from 'jquery';

export default {
	data() {
		return {
			products: [],
		}
	},
	methods: {
		getProducts() {
			const vm = this;
			const api = `${process.env.API_PATH}/api/${process.env.CUSTOM_PATH}/products`;
			this.$http.get(api).then((response) => {
				vm.products = response.data.products;
				console.log(respons);
			});
		},
		getProduct(id) {
			const vm = this;
			const api = `${process.env.API_PATH}/api/${process.env.CUSTOM_PATH}/product/${id}`;
			this.$http.get(api).then((response) => {
				vm.products = response.data.products;
				console.log(respons);
			});
		},
	},
	created() {
		this.getProducts();
	}
}
</script>

<style>
*{
	position:relative;
	box-sizing:border-box;
}
/*-----------------*/
/*quote
/*-----------------*/
.quote{
  font-family: 'Oxygen Mono', monospace;
  line-height:30px;
}
.quote span{
	display: inline-block;
}
.fa-quote-left{
	color:#c3c0c0;
	font-size:60px;
	margin-right:20px;
}
/*-----------------*/
/*shop
/*-----------------*/
.u-item-img{
	height: 250px; 
	background-size: cover; 
	background-position: center;
}
.u-item-cover{
	background-color: rgba(0,0,0,0.5);
	position:absolute;
	top:0;
	left:0;
	width:100%;
	height:100%;
	cursor:pointer;
	visibility: hidden;
	opacity:0;
	transition: all .2s;
}
.u-item-btn{
	position:absolute;
	top:50%;
	left:50%;
	transform: translate(-50%,-50%);
	padding:5px 10px;
	border:1px solid;
	color:#fff;
	text-decoration: none;
}
.u-item-img:hover .u-item-cover{
	visibility: visible;
	opacity:1;
}
.u-item-img:hover .u-item-btn{
	color:#fff;
	text-decoration: none;
}

.old{
	text-decoration: line-through;
}
.row.mt-4>div:nth-child(1){
	display: none;
}

</style>


