<template>
	<div>
		<!-- breadcrumb -->
		<nav aria-label="breadcrumb">
		  <ol class="breadcrumb">
		  	<div class="container d-flex">
		  		<li class="breadcrumb-item"><router-link to="/">首頁</router-link></li>
			    <li class="breadcrumb-item">{{product.category}}系列</li>
			    <li class="breadcrumb-item active" aria-current="page">{{product.title}}</li>
		  	</div>
		  </ol>
		</nav>
		<!-- product -->
		<div class="container">
			<div class="row justify-content-center my-4">
				<!-- image -->
				<div class="col-md-3 order-md-2 text-center">
					<img :src="product.image" class="img-fluid" alt="">
				</div>
				<!-- main text -->
				<div class="col-md-6 order-md-1 mt-4 mt-md-0">
					<h1 class="h3 mb-4">{{product.title}}</h1>
					<div class="d-flex justify-content-end">
						<div v-if="!product.price">${{ product.origin_price }}</div>
						<del v-if="product.price" class="mr-2">原價{{ product.origin_price }}</del>
						<div class="h5 text-danger" v-if="product.price">特價{{ product.price }}</div>
					</div>
	        <hr>

	        <div class="d-flex my-4">
	        	<select name="" class="col-md-7 form-control mr-1" v-model="product.num">
					<option value="0" selected disabled>請選擇商品數量</option>
					<option :value="num" v-for="num in 10" :key="num">選購 {{num}} {{product.unit}}</option>
				</select>
				<button class="btn btn-primary col-md-5 u-pointer" @click="addtoCart(product.id,product.num)">
					<i class="fas fa-plus-circle"></i>
					加入購物車
				</button>
	        </div>
				</div>
			</div>
			<!-- description -->
			<div class="row justify-content-center mb-4">
				<div class="col-10">
					<!-- text -->
					<div class="u-desc text-center">
						<h4 class="">商品說明</h4>
						<p>oasvjoasjfajfaosjfoassijgoaisjg</p>
					</div>
				</div>
			</div>
		</div>
			

	</div>
</template>

<script>
export default {
	data () {
		return {
			itemId: '',
			product: {},
			randomUrl: [],
		}
	},
	methods: {
		getProduct() {
			const vm = this;
			const url = `${process.env.API_PATH}/api/${process.env.CUSTOM_PATH}/product/${vm.itemId}`;
			vm.isLoading = true;
			this.$http.get(url).then((response) => {
				vm.product = response.data.product;
				vm.product.num = 0;
				
		});
    },

    addtoCart(id, qty = 1){
      this.$store.dispatch('addtoCart', {id, qty});
    },
  },
created() {
		this.itemId = this.$route.params.itemId;
		// console.log(this.$route.params)
		this.getProduct();
		document.body.scrollTop = 350;
   		document.documentElement.scrollTop = 350;
}
}
</script>

<style>
.u-pointer{
	cursor:pointer;
}
.u-pointer:active{
	position:relative;
	top:1px;
	left:1px;
}
.u-desc{
	padding-top:16px;
}
.u-desc p{
	line-height:24px;
}
</style>


